.img {
    width: 100px;
    height: 100px;
}

.h1-catalog {
    transition: 0.3s;
}

.h1-catalog:hover {
    color: #F3C44EFF;

}

.td {
    padding: 0px 250px 0px 250px;
}

.all {
    display: flex;
    justify-content: right;
}

.homeLink {
    width: 100%;
    max-width: var(--dl-size-size-xxlarge);
    transition: 0.3s;
}

.homeLink:hover {
    color: var(--dl-color-pallet-yellow);
}

@media (max-width: 479px) {
    .h1-catalog {
        text-orientation: upright;
        writing-mode: vertical-lr;
        transition: 0.3s;
    }
    .td {
        padding: 0px 10%;
    }


}
@media (max-width: 716px) {
    .h1-catalog {
        padding-left: var(--dl-space-space-twounits);
        padding-right: var(--dl-space-space-twounits);
    }
    .td {
        padding: 0px 20%;
        transition: 0.3s;
    }

}